/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";

import React, { useState, useEffect, useCallback, useMemo } from "react";
import { useSearchParams } from "next/navigation";
import {
  Card,
  CardHeader,
  CardContent,
  CardTitle,
  CardDescription,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import {
  Loader,
  FileText,
  FileIcon as FilePresentation,
  Download,
  RefreshCw,
  Eye,
  AlertCircle,
  Upload,
  MessageSquare,
  Edit,
  Trash2,
} from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";

interface SubmittedDocument {
  idPFE: number;
  idLivrable: 1 | 2;
  fichier: string;
  nomGroupe: string;
  validationStatus?: "validated" | "rejected" | "pending";
  comment?: string;
}

interface LivrableNames {
  [key: number]: string;
}

export default function TuteurGroupDocumentsPage() {
  const searchParams = useSearchParams();
  const idTuteur = useMemo(() => searchParams.get("idTuteur") || "10", [searchParams]);

  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [fetchError, setFetchError] = useState<string | null>(null);
  const [showValidationForm, setShowValidationForm] = useState(false);
  const [currentDoc, setCurrentDoc] = useState<SubmittedDocument | null>(null);

  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  };

  const livrableIcons: Record<number, React.ReactNode> = {
    1: <FileText className="h-4 w-4" />,
    2: <FilePresentation className="h-4 w-4" />,
  };

  const fetchDocuments = useCallback(async () => {
    setIsLoading(true);
    setFetchError(null);
    try {
      const response = await fetch(
        `http://localhost:5000/api/tut_soumettre/group-documents?idTuteur=${idTuteur}`
      );
      if (response.ok) {
        const data = await response.json();
        setSubmittedDocuments(data);
      } else {
        setFetchError("Impossible de récupérer les documents du groupe");
      }
    } catch (error) {
      setFetchError("Erreur lors de la récupération des documents");
      console.error("Fetch error:", error);
    } finally {
      setIsLoading(false);
    }
  }, [idTuteur]);

  const openValidationForm = (doc: SubmittedDocument) => {
    setCurrentDoc(doc);
    setShowValidationForm(true);
  };

  const closeValidationForm = () => {
    setShowValidationForm(false);
    setCurrentDoc(null);
  };

  const submitValidation = async (
    validationStatus: "validated" | "rejected" | "pending",
    comment: string
  ) => {
    if (!currentDoc) return;

    try {
      const response = await fetch("http://localhost:5000/api/validate_document", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          idPFE: currentDoc.idPFE,
          idLivrable: currentDoc.idLivrable,
          idTuteur,
          validationStatus,
          comment,
        }),
      });

      if (response.ok) {
        fetchDocuments(); // Refresh document list
        closeValidationForm();
      } else {
        console.error("Failed to submit validation:", await response.json());
      }
    } catch (error) {
      console.error("Error submitting validation:", error);
    }
  };

  const deleteValidation = async () => {
    if (!currentDoc) return;

    try {
      const response = await fetch(
        `http://localhost:5000/api/validate_document?idPFE=${currentDoc.idPFE}&idLivrable=${currentDoc.idLivrable}&idTuteur=${idTuteur}`,
        {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (response.ok) {
        fetchDocuments(); // Refresh document list
        closeValidationForm();
      } else {
        console.error("Failed to delete validation:", await response.json());
      }
    } catch (error) {
      console.error("Error deleting validation:", error);
    }
  };

  useEffect(() => {
    fetchDocuments();
  }, [fetchDocuments]);

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-2xl mx-auto shadow-lg border-0 overflow-hidden bg-gradient-to-b from-background to-muted/20">
        <CardHeader className="pb-6 border-b border-border/40">
          <CardTitle className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/80">
            Documents des Groupes Supervisés
          </CardTitle>
          <CardDescription className="text-muted-foreground">
            Consultez les documents soumis par les groupes que vous encadrez
          </CardDescription>
        </CardHeader>
        <CardContent className="p-6 space-y-8">
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-lg font-semibold">Documents Soumis</h2>
              <Button
                variant="outline"
                size="sm"
                onClick={fetchDocuments}
                disabled={isLoading}
                className="h-8 hover:bg-primary/5 hover:text-primary"
              >
                <RefreshCw className={`h-3.5 w-3.5 mr-1.5 ${isLoading ? "animate-spin" : ""}`} />
                Actualiser
              </Button>
            </div>

            {isLoading ? (
              <div className="flex items-center justify-center p-8 bg-muted/10 rounded-xl border border-border/40">
                <Loader className="h-5 w-5 animate-spin mr-2 text-primary" />
                <p className="text-muted-foreground">Chargement des documents...</p>
              </div>
            ) : fetchError ? (
              <Alert variant="destructive" className="bg-destructive/10 text-destructive border-destructive/20">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{fetchError}</AlertDescription>
                <Button variant="link" onClick={fetchDocuments} className="text-destructive">
                  Réessayer
                </Button>
              </Alert>
            ) : submittedDocuments.length > 0 ? (
              <div className="space-y-3">
                {submittedDocuments.map((doc) => (
                  <div
                    key={`${doc.idPFE}-${doc.idLivrable}`}
                    className="flex items-center justify-between p-4 bg-card rounded-xl border border-border/40 hover:shadow-md transition-all duration-300 hover:border-primary/20"
                  >
                    <div className="flex items-center gap-3">
                      <div className="bg-primary/10 p-2 rounded-full">
                        {livrableIcons[doc.idLivrable] ?? <FileText className="h-4 w-4" />}
                      </div>
                      <div className="space-y-1">
                        <span className="font-medium">{livrableNames[doc.idLivrable]}</span>
                        <Badge variant="outline" className="ml-2">
                          {doc.nomGroupe}
                        </Badge>
                        {doc.validationStatus && (
                          <Badge
                            variant={
                              doc.validationStatus === "validated"
                                ? "default"
                                : doc.validationStatus === "rejected"
                                ? "destructive"
                                : "secondary"
                            }
                            className="ml-2"
                          >
                            {doc.validationStatus === "validated"
                              ? "Validé"
                              : doc.validationStatus === "rejected"
                              ? "Rejeté"
                              : "En attente"}
                          </Badge>
                        )}
                        {doc.comment && (
                          <p className="text-sm text-muted-foreground">Commentaire: {doc.comment}</p>
                        )}
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button variant="ghost" size="sm" asChild>
                        <a
                          href={`${process.env.NEXT_PUBLIC_API_URL}${doc.fichier}`}
                          download={doc.fichier.split("/").pop()}
                          onClick={() =>
                            console.log("Download URL:", `${process.env.NEXT_PUBLIC_API_URL}${doc.fichier}`)
                          }
                        >
                          <Download className="h-4 w-4 mr-2" />
                          Télécharger
                        </a>
                      </Button>
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() =>
                          window.open(`${process.env.NEXT_PUBLIC_API_URL}${doc.fichier}`, "_blank")
                        }
                      >
                        <Eye className="h-4 w-4 mr-2" />
                        Prévisualiser
                      </Button>
                      <Button variant="ghost" size="sm" onClick={() => openValidationForm(doc)}>
                        <MessageSquare className="h-4 w-4 mr-2" />
                        Valider / Commenter
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center p-8 bg-muted/10 rounded-xl border border-border/40">
                <Upload className="h-8 w-8 text-muted-foreground mb-2" />
                <p className="text-muted-foreground text-center">
                  Aucun document soumis par vos groupes pour le moment.
                </p>
              </div>
            )}
          </div>
          <Separator className="my-8" />
        </CardContent>
      </Card>

      {showValidationForm && currentDoc && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md">
            <h3 className="text-lg font-semibold mb-4">
              {currentDoc.validationStatus ? "Modifier" : "Valider / Commenter"} :{" "}
              {livrableNames[currentDoc.idLivrable]}
            </h3>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1">Statut</label>
                <select
                  className="w-full p-2 border rounded"
                  defaultValue={currentDoc.validationStatus || "pending"}
                  id="validationStatus"
                >
                  <option value="pending">En attente</option>
                  <option value="validated">Validé</option>
                  <option value="rejected">Rejeté</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Commentaire</label>
                <textarea
                  className="w-full p-2 border rounded"
                  rows={4}
                  defaultValue={currentDoc.comment || ""}
                  placeholder="Entrez votre commentaire ici (optionnel)"
                  id="comment"
                />
              </div>
              <div className="flex gap-2">
                <Button
                  onClick={() => {
                    const status = (
                      document.getElementById("validationStatus") as HTMLSelectElement
                    ).value as "validated" | "rejected" | "pending";
                    const comment = (document.getElementById("comment") as HTMLTextAreaElement).value;
                    submitValidation(status, comment);
                  }}
                >
                  {currentDoc.validationStatus ? (
                    <>
                      <Edit className="h-4 w-4 mr-2" />
                      Modifier
                    </>
                  ) : (
                    "Soumettre"
                  )}
                </Button>
                {currentDoc.validationStatus && (
                  <Button variant="destructive" onClick={deleteValidation}>
                    <Trash2 className="h-4 w-4 mr-2" />
                    Supprimer
                  </Button>
                )}
                <Button variant="outline" onClick={closeValidationForm}>
                  Annuler
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 